import "./App.css";
import Graph from "react-graph-vis";
import SVG from "react-inlinesvg";
import ReactMarkdown from "react-markdown";
import React, { useState, useEffect } from "react";

const DEFAULT_PARAMS = {
  model: "text-davinci-003",
  temperature: 0.3,
  max_tokens: 800,
  top_p: 1,
  frequency_penalty: 0,
  presence_penalty: 0,
};

const options = {
  layout: {
    hierarchical: false,
  },
  edges: {
    color: "#34495e",
  },
};

const graphPrompt = "Venu works at Techgrit. Make Techgrit Green and Venu Blue";
const svgPrompt = "give me an image of a red circle.";
const travelGuidePrompt =
  "I am in Hyderabad/India and I want to visit only museums.";

function App() {
  const [state, setState] = useState({
    counter: 0,
    graph: {
      nodes: [],
      edges: [],
    },
  });
  const [inputValue, setInputValue] = useState(graphPrompt);
  const [svgValue, setSvgValue] = useState();
  const [templateOption, setTemplateOption] = useState("Graph");
  const [textOutput, setTextOutput] = useState();
  const { graph } = state;

  useEffect(() => {
    if (templateOption == "Graph") {
      setInputValue(graphPrompt);
    } else if (templateOption == "SVG designer") {
      setInputValue(svgPrompt);
    } else if (templateOption == "Travel Guide") {
      setInputValue(travelGuidePrompt);
    }
  }, [templateOption]);

  const clearState = () => {
    setState({
      counter: 0,
      graph: {
        nodes: [],
        edges: [],
      },
    });
  };

  const queryPrompt = (prompt, apiKey) => {
    fetch(window.location.href + getTemplateURL())
      .then((response) => response.text())
      .then((text) => text.replace("$prompt", prompt))
      .then((text) => text.replace("$state", JSON.stringify(state)))
      .then((prompt) => {
        console.log(prompt);

        const params = { ...DEFAULT_PARAMS, prompt: prompt };

        const requestOptions = {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + String(apiKey),
          },
          body: JSON.stringify(params),
        };
        fetch("https://api.openai.com/v1/completions", requestOptions)
          .then((response) => {
            if (!response.ok) {
              switch (response.status) {
                case 401: // 401: Unauthorized: API key is wrong
                  throw new Error("Please double-check your API key.");
                case 429: // 429: Too Many Requests: Need to pay
                  throw new Error(
                    "You exceeded your current quota, please check your plan and billing details."
                  );
                default:
                  throw new Error(
                    "Something went wrong with the request, please check the Network log"
                  );
              }
            }
            return response.json();
          })
          .then((response) => {
            handleResponse(response);
          })
          .catch((error) => {
            handleErrors(error);
          });
      });
  };

  const createGraph = () => {
    document.body.style.cursor = "wait";

    document.getElementsByClassName("generateButton")[0].disabled = true;
    const prompt = inputValue;
    const apiKey = document.getElementsByClassName("apiKeyTextField")[0].value;

    queryPrompt(prompt, apiKey);
  };

  const getTemplateURL = () => {
    if (templateOption == "Graph") {
      return "/prompts/main.prompt";
    } else if (templateOption == "SVG designer") {
      return "/prompts/svg.prompt";
    } else if (templateOption == "Travel Guide") {
      return "/prompts/travelGuide.prompt";
    } else {
      return "/prompts/main.prompt";
    }
  };

  const handleResponse = (response) => {
    if (templateOption == "Graph") {
      const { choices } = response;
      const text = choices[0].text;
      console.log(text);
      const new_graph = JSON.parse(text);
      console.log(new_graph);
      setState(new_graph, () => {
        console.log(state);
      });
    } else if (templateOption == "SVG designer") {
      const { choices } = response;
      const text = choices[0].text;
      console.log(text);
      setSvgValue(text);
    } else if (templateOption == "Travel Guide") {
      const { choices } = response;
      const text = choices[0].text;
      console.log(text);
      setTextOutput(text);
    } else {
      const { choices } = response;
      const text = choices[0].text;
      console.log(text);
      setTextOutput(text);
    }

    document.body.style.cursor = "default";
    document.getElementsByClassName("generateButton")[0].disabled = false;
    document.getElementsByClassName("searchBar")[0].value = "";
  };

  const handleErrors = (error) => {
    console.log(error);
    alert(error);
    document.body.style.cursor = "default";
    document.getElementsByClassName("generateButton")[0].disabled = false;
  };

  const readOutLoud = (text) => {
    var msg = new SpeechSynthesisUtterance();
    msg.text = text;
    window.speechSynthesis.speak(msg);
  };

  return (
    <div className="container">
      <h1 className="headerText">
        TemplateGPT (Inspired from{" "}
        <a href="https://graphgpt.vercel.app/">graphGPT</a>) 🔎
      </h1>
      <p className="subheaderText">
        LOW EFFORT Template engine to write content for emails/lindein post/chat
        messages.
      </p>
      <p className="subheaderText">
        Prompts copied from - https://github.com/f/awesome-chatgpt-prompts
      </p>
      <p className="opensourceText">
        <a href="https://github.com/varunshenoy/graphgpt">
          GraphGPT is open-source
        </a>
        &nbsp;🎉
      </p>
      <input
        className="apiKeyTextField"
        type="password"
        placeholder="Enter your OpenAI API key..."
      ></input>
      <center>
        <div className="inputContainer">
          <select
            className="category"
            placeholder="Category"
            value={templateOption}
            onChange={(e) => {
              console.log(e.target.value);
              setTemplateOption(e.target.value);
            }}
          >
            <option value={"SVG designer"}>SVG designer</option>
            <option value={"Web Write Back"}>Web Write Back</option>
            <option value={"Graph"}>Graph</option>
            <option value={"Travel Guide"}>Travel Guide</option>
            <option>Email (Leave/Client communication)</option>
            <option disabled={true}>Diagram Generator</option>
            <option disabled={true}>Mermaidx Generator</option>
            <option disabled={true}>UML Generator</option>
            <option disabled={true}>Synonym finder</option>
            <option disabled={true}>Variable Name suggestions</option>
            <option disabled={true}>ParaPhrase</option>
            <option disabled={true}>Blog</option>
            <option disabled={true}>linkedin Post</option>
            <option disabled={true}>IT Architect</option>
            <option disabled={true}>Password Generator</option>
          </select>
          <input
            className="searchBar"
            placeholder="Describe your graph..."
            value={inputValue}
            onChange={(e) => {
              setInputValue(e.target.value);
            }}
          ></input>
          <button className="generateButton" onClick={createGraph}>
            Generate
          </button>
          <button className="clearButton" onClick={clearState}>
            Clear
          </button>
        </div>
      </center>
      <div className="responseContainer">
        {templateOption === "Graph" ? (
          <>
            <div className="graphContainer">
              <Graph
                graph={graph}
                options={options}
                style={{ height: "640px" }}
              />
            </div>
            <p className="footer">
              Pro tip: don't take a screenshot! You can right-click and save the
              graph as a .png 📸
            </p>
          </>
        ) : templateOption === "SVG designer" ? (
          <div className="svgContainer">
            <ReactMarkdown children={svgValue} />
            {/* <SVG src={svgValue}>
              <img src="..." alt="fallback" />
            </SVG> */}
          </div>
        ) : (
          <div className="graphContainer">
            <p>{textOutput}</p>
            <button onClick={() => readOutLoud(textOutput)}>Read out</button>
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
